export default function App() {
  return (
    <html lang="en">
      <head>
        <Meta />
        <Links />
        <meta name="emotion-insertion-point" content="" />
      </head>
      <CacheProvider value={cache}>
        <ThemeProvider theme={theme}>
          <CssBaseline />
          <body data-theme={theme}>
            <Outlet />
            <ScrollRestoration />
            <Scripts />
            <LiveReload />
          </body>
        </ThemeProvider>
      </CacheProvider>
    </html>
  )
}
